import React from "react";
import {
  FormControl,
  RadioGroup,
  Radio,
  FormControlLabel
} from "@material-ui/core";

import NavButton from "../../components/nav-button/nav-button.component";
import { desires } from "../../store/desire.data";
import "./desire.styles.scss";

const DesirePage = ({
  activeDesire,
  onDesireChange,
  onDesireLeave,
  nextPath = "",
  history
}) => {
  return (
    <div className="page-root">
      <h2>Hva er viktig for deg å oppnå i hverdagen?</h2>
      <span className="page-description">
        Du kan velge andre ønsker senere.
      </span>

      <div className="form-column">
        {Object.keys(desires).map(key => {
          return (
            <NavButton
              wide
              option
              key={desires[key].id}
              value={desires[key].id}
              control={<Radio />}
              label={desires[key].text}
              onClick={() => {
                onDesireLeave();
                onDesireChange(desires[key].id);
                history.push(nextPath);
              }}
            >
              {desires[key].text}
            </NavButton>
          );
        })}
      </div>
      <div className="button-row">
        <NavButton secondary onClick={() => history.goBack()}>
          Forrige
        </NavButton>
      </div>
    </div>
  );
};

export default DesirePage;
